export const approuter = [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: 'Login - 登录'
    },
    component: () => import('@/components/login.vue')
  },
  {
    path: '/register',
    name: 'register',
    meta: {
      title: 'register - 注册'
    },
    component: () => import('@/components/register.vue')
  },
  {
    path: '/',
    name: '',
    component: () => import('@/components/index.vue'),
    meta: {
      title: '技术库-技术内容创作平台'
    },
    children: [
      {
        path: '/',
        name: 'main',
        component: () => import('@/components/main.vue'),
        meta: {
          title: '内容创作'
        }
      },
      {
        path: '/article',
        name: 'ArtilceList',
        component: () => import('@/components/article/list.vue'),
        meta: {
          title: '我发布的内容'
        },
        children: [
          {
            path: '/article/own',
            name: 'Artilceown',
            component: () => import('@/components/article/own.vue'),
            meta: {
              title: '全部文章',
              scrollToTop: true
            }
          },
          {
            path: '/article/Artilceownflow',
            name: 'Artilceownflow',
            component: () => import('@/components/article/ownfowl.vue'),
            meta: {
              title: '未审核文章'
            }
          },
          {
            path: '/article/published',
            name: 'published',
            component: () => import('@/components/article/published.vue'),
            meta: {
              title: '已发布文章'
            }
          }]
      },
      {
        path: '/article/comment',
        name: 'comment',
        component: () => import('@/components/article/comment.vue'),
        meta: {
          title: '内容创作'
        }
      },
      {
        path: '/article/new',
        name: 'newarticle',
        component: () => import('@/components/article/new.vue'),
        meta: {
          title: '内容创作'
        }
      },
      {
        path: '/source/images',
        title: '素材管理',
        name: 'sourceimg',
        component: () => import('@/components/source/index.vue'),
        meta: {
          title: '素材管理'
        }
      },
      {
        path: '/fans/list',
        name: 'fanslist',
        component: () => import('@/components/fans/fans.vue'),
        meta: {
          title: '粉丝管理'
        }
      },
      {
        path: '/income/statisitcs',
        name: 'incomestatistics',
        component: () => import('@/components/income/statistics.vue'),
        meta: {
          title: '收益统计'
        }
      },
      {
        path: '/income/settlement',
        name: 'settlement',
        component: () => import('@/components/income/settlement.vue'),
        meta: {
          title: '结算中心'
        }
      },
      {
        path: '/income/addaccountsback',
        name: 'addaccountsback',
        component: () => import('@/components/income/addaccountsback.vue'),
        meta: {
          title: '结算账号'
        }
      },
      {
        path: '/user/info',
        name: 'userinfo',
        component: () => import('@/components/user/info.vue'),
        meta: {
          title: '账号设置'
        },
        children: [
          {
            path: '/user/info',
            name: 'uinfo',
            component: () => import('@/components/user/userinfo.vue'),
            meta: {
              title: '详细信息'
            }
          },
          {
            path: '/user/info/setting',
            name: 'setting',
            component: () => import('@/components/user/usersetting.vue'),
            meta: {
              title: '登录设置'
            }
          }
        ]
      },
      {
        path: '/user/function',
        name: 'userfunction',
        component: () => import('@/components/user/function.vue'),
        meta: {
          title: '账号功能'
        }
      },
      {
        path: '/user/userstatus',
        name: 'userstatus',
        component: () => import('@/components/user/accountStatus.vue'),
        meta: {
          title: '账号功能'
        }
      },
      {
        path: '/message',
        name: 'message',
        component: () => import('@/components/message/index.vue'),
        meta: {
          title: '消息记录'
        },
        children: [
          {
            path: '/message/notice',
            name: 'messagenotice',
            component: () => import('@/components/message/notice.vue'),
            meta: {
              title: '通知消息'
            }
          },
          {
            path: '/message/subscribe',
            name: 'messagesubscribe',
            component: () => import('@/components/message/subscribe.vue'),
            meta: {
              title: '关注消息'
            }
          },
          {
            path: '/message/comment',
            name: 'messagecomment',
            component: () => import('@/components/message/comment.vue'),
            meta: {
              title: '评论通知'
            }
          }
        ]
      }
    ]
  }
]

export const routers = [
  ...approuter
]



